import * as React from 'react';
import { StyleSheet, View, Text } from 'react-native';
import BtnCircle from '../components/buttons/BtnCircle';
import { PropsStackDiscFront, RootStackParamList } from './../types';
import Details from '../components/Cards/Details';
export default function DiscFront({route, navigation}: PropsStackDiscFront ){
    // Função Referencia
    let openControlPanel = () => {
      navigation.navigate('DiscBack', {aminoAcid: 'ala'}) // open drawer
    };


    return (
      <View style={styles.main}>
        <Details
        btn={<BtnCircle
          name="Go to Details"
          icon="info"
          onPress={() => openControlPanel()}
        />}
        describePrimary = "Altamente hidrofóbico; comum em alfa hélices e folhas beta."
        descrebeSecundary='Aminoácido essencial; cadeia lateral composta por um um anel aromático (grupo fenil) ligado a uma metila; absorve muito pouco de luz ultravioleta.'
        ></Details>
        <Details
        btn={<BtnCircle
          name="Go to Details"
          icon="info"
          onPress={() => openControlPanel()}
        />}
        describePrimary = "Altamente hidrofóbico; comum em alfa hélices e folhas beta."
        descrebeSecundary='Aminoácido essencial; cadeia lateral composta por um um anel aromático (grupo fenil) ligado a uma metila; absorve muito pouco de luz ultravioleta.'
        ></Details>
        <Details
        btn={<BtnCircle
          name="Go to Details"
          icon="info"
          onPress={() => openControlPanel()}
        />}
        describePrimary = "Altamente hidrofóbico; comum em alfa hélices e folhas beta."
        descrebeSecundary='Aminoácido essencial; cadeia lateral composta por um um anel aromático (grupo fenil) ligado a uma metila; absorve muito pouco de luz ultravioleta.'
        ></Details>
        
      </View>
    );
  }
  const styles = StyleSheet.create({
    main:{
      backgroundColor:"#ca29d5",
      flex: 1, 
      margin:50,
    
      justifyContent: 'space-evenly',
      flexDirection:'row'
    }
  });
