

import * as React from 'react';
import { Button, View, Text } from 'react-native';
import { PropsStackShared, RootStackParamList } from './../types';


export default function DiscBack({ navigation}: PropsStackShared ) {
    return (
      <View style={{ flex: 1, alignItems: 'center', justifyContent: 'center' }}>
        <Text>DiscBack: Details Screen</Text>
        <Button
          title="Go to Details"
          onPress={() => navigation.navigate('DiscFront', {aminoAcid: null})}
        />
      </View>
    );
  }
  