import * as React from 'react';
import { View, Text, StyleSheet } from 'react-native';
import { PropsDetails } from '../../types';
import  {themeColors}  from '../../constants/Colors';

export default function Details(props : PropsDetails ) {
    return (
      <View style={styles.container}>
          <View style={styles.text}>
        <Text style={styles.descrebePrimary}>{props.describePrimary}</Text>
        <Text style={styles.descrebeSecundary}>{props.descrebeSecundary}</Text>
        </View>
        <View style={styles.btn}>{props.btn}</View>
      </View>
    );
  }
  
  const styles = StyleSheet.create({
    container: {
      flex: 1,
      justifyContent: 'space-between',
      padding: 20,
      backgroundColor:themeColors.default.white,
      borderRadius: 10
    },
    descrebePrimary:{
        backgroundColor:themeColors.default.color1,
        color:themeColors.default.white,
        fontSize:themeColors.default.par2,
        padding: 10,
        borderStyle: 'solid',
        borderColor:'black',
        borderWidth:2,
        borderRadius: 10

    }, 
    descrebeSecundary:{
        color: themeColors.default.black,
        fontSize:themeColors.default.par2,
        
    },
    btn:{
        height:'min-height',
        position:'relative',
        bottom: '-70px'
    },
    text:{
        flexGrow:1,
        flex:1,
        justifyContent:'space-evenly'
    }
  });
