import * as React from 'react';
import { Button, View, Text } from 'react-native';

import { NavigationContainer } from '@react-navigation/native';
import { createNativeStackNavigator } from '@react-navigation/native-stack';
import { RootStackParamList } from './types';

// Screens base app
import DiscBack from './screens/DiscBack';
import DiscFront from './screens/DiscFront';
import Instructions from './screens/Instructions';
import Shared from './screens/Shared';
import Credits from './screens/Credits';


// Resorcs
import NavBarContainer from './components/Nav/NavBarContainer';



const Stack = createNativeStackNavigator<RootStackParamList>();

function App() {
  return (
    <NavigationContainer>
      <Stack.Navigator initialRouteName="DiscFront">
        <Stack.Screen name="DiscFront" component={DiscFront} />
        <Stack.Screen name="DiscBack" component={DiscBack} />
        <Stack.Screen name="Instructions" component={Instructions} />
        <Stack.Screen name="Shared" component={Shared} />
        <Stack.Screen name="Credits" component={Credits} />
      </Stack.Navigator>
      <NavBarContainer/>
    </NavigationContainer>
  );
}



export default App;