import React from 'react';
import {FontAwesome} from '@expo/vector-icons';
import  {themeColors}  from '../../constants/Colors';
import { StyleSheet, Text, View, TouchableOpacity } from 'react-native';
import { PropsButtuns } from '../../types';

function BtnCircle(props : PropsButtuns) {
    
    // Função ancora
    let onPress = () => {
      props.onPress();
    };

    return (
      <TouchableOpacity 
      onPress={() => onPress()}
      style={styles.btnCircle}
      >
        <Text style={styles.iconContainer}>
        <FontAwesome 
        name={props.icon}
        style={
          styles.icon
        }
        
        />
        </Text>
        <Text style={styles.title}>{props.name}</Text>
      </TouchableOpacity>
        
  
      
    );
  }


  const styles = StyleSheet.create({
    btnCircle: {
      width:"fit-content",
      height:"fit-content",
      alignItems:"center",
    },
    icon:{
      width:'100%',
      height:'100%',


      flex: 1,
      display:'flex',
      flexDirection:'column',
      justifyContent: 'center',

      color:themeColors.default.white,

      
      textAlign:'center',
      fontSize: 24
    },
    iconContainer:{
      backgroundColor:themeColors.default.color1,
      color:themeColors.default.white,
      width: '56px',
      height: '56px', 
      borderRadius: 50,
      shadowColor: '#000000',
      shadowOffset: {width: 0, height: 4},
      shadowOpacity: 0.25,
      shadowRadius: 4,
    }
    , title:{
      marginTop:'5px',
      fontSize: themeColors.default.par3,
      color: themeColors.default.gray

    }
  });

  export default BtnCircle;