

import * as React from 'react';
import { StyleSheet, Image, View, Text, BackHandler } from 'react-native';
import { themeColors } from '../../constants/Colors';
import BtnCircle from '../buttons/BtnCircle';
import { useLinkTo  } from '@react-navigation/native';



export default function NavBarContainer() {
    const link = useLinkTo ();
    let logo = require('./../../assets/logo.png');

// Função Referencia
    let openHelp = () => {
        link('/Instructions') // open drawer
    };
    let openCredits = () => {
        link('/Credits') // open drawer
    };
    let openShared = () => {
        link('/Shared') // open drawer
    };

    return (
      <View style={styles.bar} >
          <View style={styles.logo}>
              
              <Image source={logo} style={styles.logoImage} resizeMode ='contain' /> 
              
          </View>
          <View style={styles.buttons}>
            <BtnCircle
            name='Ajuda'
            icon='help'
            onPress={openHelp()}
            />
            <BtnCircle
            name='Créditos'
            icon='group'
            onPress={openCredits()}
            />
            <BtnCircle
            name='Compartilhar'
            icon='shared'
            onPress={openShared()}
            />
          </View>
                
      </View>
    );
  }
  

  const styles = StyleSheet.create({
    bar:{
        height: '100px',
        backgroundColor: themeColors.default.white,
        flexDirection:'row',
        alignItems:'center',
        justifyContent:'space-evenly'
    },
    buttons:{
        flex:1,
        flexDirection:'row',
        alignItems:'center',
        justifyContent:'space-evenly'
    },
    logo:{
        flex:1,
        justifyContent:'flex-start',
        height:"100px",
        width:"100%"
    },
    logoImage:{
        width: 'auto', 
        height:'100%'
    }
  });
